import React, { Component } from 'react'
import { Table, Divider, Tag, Card, Descriptions, InputNumber,Button, Icon, Typography, Tabs, Badge, Popconfirm } from 'antd'
import store from '../../../../store'
import isEmpty from '../../../../utils/isEmpty'
import electric from '../../../../electric.png'
import waterandgas from '../../../../waterandgas.png'
import internet from '../../../../internet.png'

class ItemList extends Component {

    constructor(props) {
        super(props)
        this.state = {}
    }


    componentDidMount() {
        localStorage.setItem('sideLocation', window.location.pathname.split('/')[1])
        store.dispatch({
            type: "TOOGLE_SIDERBAR",
            payload: "itemlist"
        })
    }

    //Tab change callback  项目标签切换回调
    onTabChange = key => {
        console.log('Tab change callback ' + key)
    }

    onNumChange = value =>{
        console.log(value)
    }

    render() {


        const { TabPane } = Tabs
        const { Title, Paragraph, Text } = Typography
        const { Meta } = Card

        //selected list  secondery 已选项目详细表 二级目录

        const expandedRowRender = () => {
            const columns = [
                { title: '项目', dataIndex: 'item', key: 'item' },
                { title: '规格', dataIndex: 'specification', key: 'specification' },
                {
                    title: '数量', dataIndex: 'num', key: 'num', render: () => (
                        <span>
                            <InputNumber  defaultValue={1} onNumChange={this.onNumChange} />
                        </span>
                    )
                },
                { title: '单价', dataIndex: 'price', key: 'price' },
                { title: '小计', dataIndex: 'cost', key: 'cost' },
                // {
                //     title: 'Status',
                //     key: 'state',
                //     render: () => (
                //         <span>
                //             <Badge status="success" />
                //             Finished
                // </span>
                //     ),
                // },

            ];

            const data = [];
            for (let i = 0; i < 3; ++i) {
                data.push({
                    item: '电力',
                    specification: '380V/30A',
                    num: '2',
                    price: '1800',
                    cost: '3600',
                });
            }
            return <Table columns={columns} dataSource={data} pagination={false} />;
        }

        //base cost jsx

        const baseCost = (
            <div style={{ margin: '30px 20px 5px 20px', width: '80%' }}>
                <Descriptions title={'展位号：A2A02'} bordered='true'>
                    <Descriptions.Item label="展位面积" span={2}>{72} ㎡</Descriptions.Item>
                    <Descriptions.Item label="施工押金">{10000} 元</Descriptions.Item>
                    <Descriptions.Item label="施工管理费" span={2}>{2000}元</Descriptions.Item>
                    <Descriptions.Item label="审图费">{1200} 元</Descriptions.Item>
                    <Descriptions.Item label="证件费">{200}元</Descriptions.Item>
                </Descriptions>
            </div>
        )

        //item list main data
        const itemListColumns = [
            { title: '展位号', dataIndex: 'booth_number', key: 'booth_number', align: 'center' },
            { title: '面积(㎡)', dataIndex: 'booth_size', key: 'booth_size', align: 'center' },
            { title: '展商名称', dataIndex: 'booth_owner', key: 'booth_owner', align: 'center' },
            { title: '服务公司', dataIndex: 'booth_server', key: 'booth_server', align: 'center' },
            { title: '费用总计', dataIndex: 'booth_cost', key: 'booth_cost', align: 'center' },
            {
                title: '', key: 'operation', render: () => <div>
                    <Popconfirm title="Are you sure？" okText="Yes" cancelText="No">
                        <a href='#'>删除</a>
                    </Popconfirm>
                    <Divider type='vertical' />
                    <a href='#'>提交</a></div>
            },
        ];

        const itemListData = [];
        for (let i = 0; i < 3; ++i) {
            itemListData.push({
                key: i,
                booth_number: 'A2A012',
                booth_size: '72',
                booth_owner: '阿里云技术服务有限公司',
                booth_server: '武汉多人行展览服务有限公司',
                booth_cost: '23100',
            });
        }

        //electric Card jsx
        const electricCard = (<Card
            size='small'
            style={{ width: 250, height: 270 }}
            cover={
                <img
                    alt="electric"
                    src={electric}
                    style={{ height: 150, width: 200, marginLeft: '25px' }}
                />
            }
            actions={[
                <Icon type="minus" key="minus" />,
                '1',
                <Icon type="plus" key="plus" />,
            ]}
        >
            <Meta
                title="展期用电"
                description="380V/30A"
            />
        </Card>)

        //waterandgas Card
        const waterandgasCard = (<Card
            size='small'
            style={{ width: 250, height: 270 }}
            cover={
                <img
                    alt="waterandgas"
                    src={waterandgas}
                    style={{ height: 150, width: 180, marginLeft: '35px' }}
                />
            }
            actions={[
                <Icon type="minus" key="minus" />,
                '1',
                <Icon type="plus" key="plus" />,
            ]}
        >
            <Meta
                title="压缩空气"
                description="10Mpa"
            />
        </Card>)

        //internet Card
        const internetCard = (<Card
            size='small'
            style={{ width: 250, height: 270 }}
            cover={
                <img
                    alt="internet"
                    src={internet}
                    style={{ height: 150, width: 180, marginLeft: '35px' }}
                />
            }
            actions={[
                <Icon type="minus" key="minus" />,
                '1',
                <Icon type="plus" key="plus" />,
            ]}
        >
            <Meta
                title="网络宽带"
                description="20M"
            />
        </Card>)

        //Item Selected list
        const columns = [
            {
                title: '类别',
                dataIndex: 'rent_item',
                key: 'rent_item',
                render: text => <Text>{text}</Text>,
            },
            {
                title: '明细',
                dataIndex: 'rent_item_detail',
                key: 'rent_item_detail',
                render: tags => (
                    <span>
                        {tags.map(tag => {
                            let color = tag.length > 5 ? 'geekblue' : 'green';
                            if (tag === 'loser') {
                                color = 'volcano';
                            }
                            return (
                                <Tag color={color} key={tag}>
                                    {tag.toUpperCase()}
                                </Tag>
                            );
                        })}
                    </span>
                )
            },
            {
                title: '数量',
                dataIndex: 'rent_item_number',
                key: 'rent_item_number',
            },
            {
                title: '金额',
                key: 'rent_item_cost',
                dataIndex: 'rent_item_cost',
            },
            {
                title: '操作',
                key: 'rent_item_operate',
                render: (text, record) => (
                    <span>
                        <Button type='primary'>提交 {record.name}</Button>
                        <Divider type="vertical" />
                        <Button type='danger' >删除</Button>
                    </span>
                ),
            },
        ]

        const data = [
            {
                key: '1',
                rent_item: '用电',
                rent_item_detail: ['380V/30A'],
                rent_item_number: '1',
                rent_item_cost: '2000',
            },
            {
                key: '2',
                rent_item: '网络宽带',
                rent_item_detail: ['20M'],
                rent_item_number: '1',
                rent_item_cost: '3500',
            }

        ]

        return (
            <div style={{ backgroundColor: 'white', paddingBottom: '200px' }}>
                <Typography style={{ paddingTop: '20px', paddingLeft: '15px' }}>
                    <Title level={4} >| 工程租赁</Title>
                    <Paragraph style={{ textIndent: '8px' }}>
                        请根据展位需求添加相应的<Text mark > 工程项目 </Text>
                    </Paragraph>
                </Typography>
                <Tabs defaultActiveKey="1" onChange={this.onTabChange} style={{ height: '380px' }}>
                    <TabPane tab="展位基本费用" key="baseCost">
                        {baseCost}
                        <Button type='primary' style={{ float: 'right', marginRight: '20%', marginTop: '10px' }}>加入清单</Button>
                    </TabPane>
                    <TabPane tab="电力租赁" key="electric">
                        {electricCard}
                    </TabPane>
                    <TabPane tab="用水及用气" key="gasandwater">
                        {waterandgasCard}
                    </TabPane>
                    <TabPane tab="网络宽带" key="internet">
                        {internetCard}
                    </TabPane>
                </Tabs>
                {/* <Table columns={columns} dataSource={data} /> */}
                <Table
                    className="components-table-demo-nested"
                    columns={itemListColumns}
                    expandedRowRender={expandedRowRender}
                    dataSource={itemListData}
                    pagination={false}
                />
                <Button type='primary' style={{ marginLeft: '45%', marginTop: '15px' }}>结算订单</Button>
            </div>
        )
    }
}

export default ItemList