import React, { Component } from 'react'
import { Typography, Spin, Divider, Form, Input, Modal, Checkbox, Button, AutoComplete, Tooltip, Icon, message } from 'antd'
import store from '../../../../store'
// import isEmpty from '../../../../utils/isEmpty'
import axios from 'axios'

import { connect } from 'react-redux'
import PropTypes from 'prop-types'
import companyToUpload from '../../../../actions/companyToUpload.action'
import {UPLOAD_TO_COMPANY,COMPANY_TO_UPLOAD} from '../../../../actions/types'

import CustomUploads from '../../../customUploads.component'
import QualifiedList from './QualifiedList.component'

class Qualification extends Component {

    constructor(props) {
        super(props)
        this.state = {
            isLoading: true,
            confirmDirty: false,
            autoCompleteResult: [],
            visible: false,
            startUploading: false,
            showCompany: true,
            company: {
                comAddr: "",
                comBankAccount: "",
                comBankName: "",
                comCredit: "",
                comName: "",
                comTel: "",
                creditImg: [],
                constructionImg: [],
                isAuthorized: {
                    CrAu: false,
                    //isConstructionAuthorized
                    CoAu: false,
                    //auth error message
                    erMsg: {
                        Cr: '',
                        Co: ''
                    }
                }
            }
        }
    }

    componentDidMount() {
        localStorage.setItem('sideLocation', window.location.pathname.split('/')[1])
        store.dispatch({
            type: "TOOGLE_SIDERBAR",
            payload: "qualification"
        })
        // if(this.props.companyFromStore.to){
        //     this.setState({
        //         ...this.state,
        //         showCompany:false
        //     })
        // }

        axios.get('/api/user/company')
            .then(res => {
                this.setState({
                    isLoading: false
                })
                if (res.data.comName) {
                    this.setState({
                        ...this.state,
                        company: res.data,
                    })
                } else {
                    this.setState({
                        ...this.state,
                        showCompany: false
                    })
                }

            })
            .catch(err =>{
                message.error('请先上传公司信息!')
                this.props.companyToUpload(COMPANY_TO_UPLOAD,{})
                this.setState({
                    isLoading: false
                })
            })

    }

    // ======================= Modal controller

    showModal = () => {
        this.setState({
            ...this.state,
            visible: true,
        })
    };

    handleOk = e => {
        console.log(e);
        this.setState({
            ...this.state,
            visible: false,
        });
    };

    handleCancel = e => {
        console.log('failed', e);
        this.setState({
            ...this.state,
            visible: false,
        });
    }

    // ======================= Form controller
    handleSubmit = e => {
        e.preventDefault();
        this.props.form.validateFieldsAndScroll((err, values) => {
            console.log(this.props.form.getFieldValue('creditImg'))
            if (!err && this.props.form.getFieldValue('agreement')) {
                console.log('Received values of form: ', values)
                console.log(this.props.form.getFieldValue('creditImg'))
                this.setState({
                    ...this.state,
                    startUploading: true
                })
                axios.post('/api/user/company', { formData: values })
                    .then(res => {
                        console.log(res)
                        axios.get('/api/user/company')
                            .then(res => {
                                this.props.companyToUpload(UPLOAD_TO_COMPANY,res.data)
                                this.setState({
                                    isLoading: false
                                })
                                if (res.data.comName) {
                                    this.setState({
                                        ...this.state,
                                        company: res.data,
                                        showCompany: true,
                                    })
                                }

                            })
                            .catch(err => console.log(err))
                        this.setState({
                            showCompany: true
                        })
                    }).catch(err => {
                        message.error(err.response)
                        console.log(err.response)
                    })
            } else if (!this.props.form.getFieldValue('agreement')) {
                message.error('请阅读并同意《平台使用规范及信息服务协议》')
            } else {
                message.error('请完整表格信息!')
            }
        })
        // const formFields = ['company_name', 'company_tel', 'company_credit', 'company_bank', 'company_addr', 'company_account']
        // const formData = this.props.form.getFieldsValue(formFields)
        // console.log(formData)
        // if (this.props.form.getFieldValue('agreement')) {
        //     console.log('2222222222222222222')
        //     axios.post('/api/user/qualification', { formData })
        //         .then(res => {
        //             console.log(res)
        //         }).catch(err => {
        //             console.log(err)
        //         })
        // }
    }

    handleConfirmBlur = e => {
        const { value } = e.target;
        this.setState({ confirmDirty: this.state.confirmDirty || !!value });
    }

    compareToFirstPassword = (rule, value, callback) => {
        const { form } = this.props;
        if (value && value !== form.getFieldValue('password')) {
            callback('Two passwords that you enter is inconsistent!');
        } else {
            callback();
        }
    }

    validateToNextPassword = (rule, value, callback) => {
        const { form } = this.props;
        if (value && this.state.confirmDirty) {
            form.validateFields(['confirm'], { force: true });
        }
        callback();
    }

    handleWebsiteChange = value => {
        let autoCompleteResult
        if (!value) {
            autoCompleteResult = []
        } else {
            autoCompleteResult = ['.com', '.org', '.net'].map(domain => `${value}${domain}`)
        }
        this.setState({ autoCompleteResult });
    }


    render() {
        const { Title, Paragraph, Text } = Typography




        const AutoCompleteOption = AutoComplete.Option
        const { getFieldDecorator } = this.props.form
        const { autoCompleteResult } = this.state

        const formItemLayout = {
            labelCol: {
                xs: { span: 12 },
                sm: { span: 4 },
            },
            wrapperCol: {
                xs: { span: 12 },
                sm: { span: 8 },
            },
        }
        const tailFormItemLayout = {
            wrapperCol: {
                xs: {
                    span: 24,
                    offset: 0,
                },
                sm: {
                    span: 16,
                    offset: 8,
                },
            },
        }

        const websiteOptions = autoCompleteResult.map(website => (
            <AutoCompleteOption key={website}>{website}</AutoCompleteOption>
        ))

        return (
            this.state.isLoading ?
                <Spin
                    style={{ marginLeft: '45%', marginTop: '20%' }}
                    tip="数据加载中..." size='large'>
                </Spin> :
                <div>
                    <Typography style={{ paddingTop: '20px', paddingLeft: '15px' }}>
                        <Title level={4} >| 公司资质认证</Title>
                        <Paragraph style={{ textIndent: '8px' }}>
                            公司资质认证提供展位费用的<Text mark > 结算主体对象 </Text>及<Text mark> 发票报销 </Text>的信息内容，请用户仔细填写相关信息，并确保真实有效！
                    </Paragraph>
                    </Typography>
                    <Divider />
                    {
                        this.state.showCompany && this.props.companyFromStore.to ?
                        <div style={{ height: '100%' }}>
                            <Form {...formItemLayout} onSubmit={this.handleSubmit} style={{ backgroundColor: 'white', paddingBottom: '50px' }}>
                                <Form.Item label="单位名称">
                                    {getFieldDecorator('comName', {
                                        rules: [
                                            {
                                                required: true,
                                                message: '请输入单位名称!',
                                            },
                                        ],
                                    })(<Input />)}
                                </Form.Item>
                                <Form.Item label="单位地址" >
                                    {getFieldDecorator('comAddr', {
                                        rules: [
                                            {
                                                required: true,
                                                message: '请输入单位地址!',
                                            },
                                        ],
                                    })(<Input />)}
                                </Form.Item>
                                <Form.Item label="电 话" >
                                    {getFieldDecorator('comTel', {
                                        rules: [
                                            {
                                                required: true,
                                                message: '请输入电话!',
                                            },
                                        ],
                                    })(<Input />)}
                                </Form.Item>
                                <Form.Item label='信用代码'>
                                    {getFieldDecorator('comCredit', {
                                        rules: [{ required: true, message: '请输入单位信用代码!', whitespace: true }],
                                    })(<Input />)}
                                </Form.Item>
                                <Form.Item label="单位银行账户">
                                    {getFieldDecorator('comBankAccount', {
                                        rules: [{ required: true, message: '请输入单位银行账户!' }],
                                    })(<Input />)}
                                </Form.Item>
                                <Form.Item label="单位开户行">
                                    {getFieldDecorator('comBankName', {
                                        rules: [{ required: true, message: '请输入单位开户行!' }],
                                    })(
                                        <AutoComplete
                                            dataSource={websiteOptions}
                                            onChange={this.handleWebsiteChange}
                                        >
                                            <Input />
                                        </AutoComplete>
                                    )}
                                </Form.Item>

                                <Form.Item label={
                                    <span>
                                        合法营业执照证明&nbsp;
                                        <Tooltip title="请使用*.jpg/jpeg/png格式上传">
                                            <Icon type="question-circle-o" />
                                        </Tooltip>
                                    </span>} >
                                    {getFieldDecorator('creditImg', {
                                        rules: [{ required: true, message: '请上传营业执照!' }],
                                        valuePropName: 'filelist',
                                    })(
                                        <div>
                                            <CustomUploads
                                                style={{ display: 'inline-block' }}
                                                startUploading={this.state.startUploading}
                                                uploadUrl='api/uploads/qualification'
                                                fileName='creditImg'
                                                previewStyle='picture'
                                                previewClassName='upload-list-inline-qualification'
                                            />
                                        </div>
                                    )}
                                </Form.Item>

                                <Form.Item label={<span>
                                    施工工程资质证明&nbsp;
                                        <Tooltip title="请使用*.jpg/jpeg/png格式上传">
                                        <Icon type="question-circle-o" />
                                    </Tooltip>
                                </span>} >
                                    {getFieldDecorator('constructionImg', {
                                        rules: [{ required: true, message: '请上传工程资质!' }],
                                        valuePropName: 'filelist'
                                    })(
                                        <div>
                                            <Tooltip title="最多可同时上传3个文件" placement="right">
                                                <div>
                                                    <CustomUploads
                                                        style={{ display: 'inline-block' }}
                                                        startUploading={this.state.startUploading}
                                                        uploadUrl='api/uploads/qualification'
                                                        fileName='constructionImg'
                                                        previewStyle='picture'
                                                        previewClassName='upload-list-inline-qualification'
                                                        isMultiple={true}
                                                    />
                                                </div>
                                            </Tooltip>
                                        </div>
                                    )}
                                </Form.Item>
                                <Form.Item {...tailFormItemLayout}>
                                    {getFieldDecorator('agreement', {
                                        valuePropName: 'checked',
                                    })(
                                        <Checkbox>
                                            我已阅读并同意 <a href='#' onClick={this.showModal}>《平台使用规范及信息服务协议》</a>
                                        </Checkbox>,
                                    )}
                                </Form.Item>
                                <Form.Item {...tailFormItemLayout}>
                                    <Button type="primary" htmlType="submit" onSubmit={e => this.handleSubmit(e)}>
                                        提交
                        </Button>
                                </Form.Item>
                            </Form>
                            <Modal
                                title="平台使用协议"
                                visible={this.state.visible}
                                onOk={this.handleOk}
                                onCancel={this.handleCancel}
                            >
                                <p>第一条...</p>
                                <p>第二条...</p>
                                <p>第三条...</p>
                            </Modal>
                        </div> :
                        <QualifiedList
                            company={this.state.company}
                        />
                    }
                </div>
        )
    }
}
const WrappedQualificationForm = Form.create({})(Qualification)

const mapStateToProps = state => ({
    companyFromStore: state.companyToUpload
})

WrappedQualificationForm.propTypes = {
    companyFromStore: PropTypes.object.isRequired,
    companyToUpload: PropTypes.func.isRequired
}

export default connect(mapStateToProps, { companyToUpload })(WrappedQualificationForm)
